import React, { useEffect, useState } from 'react';
import { Accordion, Card, ListGroupItem, ListGroup } from 'react-bootstrap'
import style from './Shop.module.css'
import firebase from 'firebase'

export default function Shop(props) {
    const [products, setProducts] = useState([])
    useEffect(() => {
        return firebase.firestore().collection("product").onSnapshot((snapshot) => {
            const productsData = [];
            snapshot.forEach(doc => productsData.push({ ...doc.data(), id: doc.id }));
            setProducts(productsData)
        })
    }, [])
    const [brendArr, setBrendArr] = useState([])
    useEffect(() => {
        firebase.firestore().collection("productCategory").doc("brendName")
            .onSnapshot((doc) => {
                const arr = []
                for (let index = 0; index < doc.data().brendName.length; index++) {
                    const element = doc.data().brendName[index];
                    arr.push(element)
                }
                setBrendArr(arr)
            });

    }, [])
    return (
        <>
            <div className={style.container}>
                <div className={style.menu}>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0" >
                                Brend
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                    {
                                        brendArr && brendArr.map(p => {
                                            return (
                                                <div>
                                                    <label>
                                                        <input name={p} onChange={(e) => { console.log(e.target.name, e.target.checked) }} className="m-2" type='checkbox' />
                                                        <span className="m-2">{p}</span>
                                                    </label>
                                                </div>
                                            )
                                        })
                                    }
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                Type
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>Hello! I'm the body</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                            Size Wheel
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>Hello! I'm the body</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                            Size Frame
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>Hello! I'm the body</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                            Frame Material
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>Hello! I'm the body</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                </div>
                <div className={style.body}>
                    {
                        products && products.map(p => {
                            return (
                                <Card style={{ width: '33%', margin: '.5px' }}>
                                    <Card.Img variant="top" src={p.productImage ? p.productImage : null} />
                                    <ListGroup className="list-group-flush">
                                        <ListGroupItem>brendName: {p.brendName}</ListGroupItem>
                                        <ListGroupItem>price: {p.price}</ListGroupItem>
                                        <ListGroupItem>Vestibulum at eros</ListGroupItem>
                                    </ListGroup>
                                    <Card.Body>
                                        <Card.Title>Card Title</Card.Title>
                                        <Card.Text>
                                            Some quick example text to build on the card title and make up the bulk of
                                            the card's content.
                                        </Card.Text>
                                    </Card.Body>

                                    <Card.Body>
                                        <Card.Link >Card Link</Card.Link>
                                        <Card.Link >Another Link</Card.Link>
                                    </Card.Body>
                                </Card>
                            )
                        })
                    }
                </div>
            </div>

        </>
    )
}