import React, { useEffect, useState } from 'react';
import { Nav } from 'react-bootstrap'
import { NavLink, Route } from 'react-router-dom';
import style from './Admin.module.css'
import { Row, Col, Table } from 'react-bootstrap'
import firebase from '../../firebaseConfig';
import { createUUID } from '../../Shared/UUID';

export default function Admin(props) {
    return (
        <>
            <div className={style.container}>
                <div className={style.menu}>
                    <Nav className="flex-column"  >
                        <Nav.Item>
                            <NavLink className="nav-link" to={"/admin/products"} >Products</NavLink>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="link-2">Option 2</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="link-3">Option 3</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="link-4">Option 4</Nav.Link>
                        </Nav.Item>
                    </Nav>
                </div>
                <div className={style.body}>
                    <Route exact path='/admin/products' render={() => <Products />} />
                </div>
            </div>
        </>
    )
}

const Products = (props) => {
    const [brendName, setBrendName] = useState('')
    const [type, setType] = useState('')
    const [sizeWheel, setSizeWheel] = useState('')
    const [sizeFrame, setSizeFrame] = useState('')
    const [frameMaterial, setFrameMaterial] = useState('')
    const [price, setPrice] = useState('')
    const [productImage, setProductImage] = useState('')
    const [progress, setProgress] = useState(0)
    const [products, setProducts] = useState([])
    useEffect(() => {
        return firebase.firestore().collection("product").onSnapshot((snapshot) => {
            const productsData = [];
            snapshot.forEach(doc => productsData.push({ ...doc.data(), id: doc.id }));
            setProducts(productsData)
        })
    }, [])
    const upload = (e) => {
        if (props.photoURL) {
            const desertRef = firebase.storage().refFromURL(props.photoURL)
            desertRef.delete().then(() => {
                console.log('Old file deleted successfully');
            }).catch((error) => {
                console.log('Old file deleted error!', error);
            });
        }
        const file = e.target.files[0]
        console.log('Upload works ');
        const filePath = `${createUUID()}.${file.type.split('/')[1]}`;
        const storageRef = firebase.storage().ref().child('productImage/' + filePath)
        const uploadTask = storageRef.put(file);
        uploadTask.on('state_changed',
            (snapshot) => {
                const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                setProgress(progress)
            }, (error) => {
                console.log('Upload error ', error);
            }, () => {
                uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
                    console.log('File available at', downloadURL);
                    setProductImage(downloadURL)
                });
            }
        );
        console.log('productImage', productImage);
    }
    const addProductToBD = (e) => {
        e.preventDefault();
        firebase.firestore().collection("product").add({
            brendName,
            type,
            price,
            sizeFrame,
            sizeWheel,
            frameMaterial,
            dateAdd: new Date(),
            productImage,
        })
            .then((docRef) => {
                setBrendName('')
                setType('')
                setSizeWheel('')
                setSizeFrame('')
                setFrameMaterial('')
                setPrice('')
                setProductImage('')
                console.log("Document written with ID: ", docRef.id);
            })
            .catch((error) => {
                console.error("Error adding document: ", error);
            });
    }
    // 1111111111
    const [brendArr, setBrendArr] = useState([])
    useEffect(() => {
        firebase.firestore().collection("productCategory").doc("brendName")
            .onSnapshot((doc) => {
                const arr = []
                for (let index = 0; index < doc.data().brendName.length; index++) {
                    const element = doc.data().brendName[index];
                    arr.push(element)
                }
                setBrendArr(arr)
            });

    }, [])
    const changeBrend = (e) => {
        setBrendName(e.currentTarget.value)
        console.log(e.currentTarget.value)
    }

    //   2222222222222222
    const [typeArr, setTypeArr] = useState([])
    useEffect(() => {
        firebase.firestore().collection("productCategory").doc("type")
            .onSnapshot((doc) => {
                const arr = []
                for (let index = 0; index < doc.data().type.length; index++) {
                    const element = doc.data().type[index];
                    arr.push(element)
                }
                setTypeArr(arr)
            });

    }, [])
    const changeType = (e) => {
        setType(e.currentTarget.value)
        console.log(e.currentTarget.value)
    }
    //   33333333333333
    const [sizeWheelArr, setSizeWheelArr] = useState([])
    useEffect(() => {
        firebase.firestore().collection("productCategory").doc("sizeWheel")
            .onSnapshot((doc) => {
                const arr = []
                for (let index = 0; index < doc.data().sizeWheel.length; index++) {
                    const element = doc.data().sizeWheel[index];
                    arr.push(element)
                }
                setSizeWheelArr(arr)
            });

    }, [])
    const changeSizeWheel = (e) => {
        setSizeWheel(e.currentTarget.value)
        console.log(e.currentTarget.value)
    }
    //   4444444444444
    const [sizeFrameArr, setSizeFrameArr] = useState([])
    useEffect(() => {
        firebase.firestore().collection("productCategory").doc("sizeFrame")
            .onSnapshot((doc) => {
                const arr = []
                for (let index = 0; index < doc.data().sizeFrame.length; index++) {
                    const element = doc.data().sizeFrame[index];
                    arr.push(element)
                }
                setSizeFrameArr(arr)
            });

    }, [])
    const changeSizeFrame = (e) => {
        setSizeFrame(e.currentTarget.value)
        console.log(e.currentTarget.value)
    }
    //   5555555555555
    const [frameMaterialArr, setFrameMaterialArr] = useState([])
    useEffect(() => {
        firebase.firestore().collection("productCategory").doc("frameMaterial")
            .onSnapshot((doc) => {
                const arr = []
                for (let index = 0; index < doc.data().frameMaterial.length; index++) {
                    const element = doc.data().frameMaterial[index];
                    arr.push(element)
                }
                setFrameMaterialArr(arr)
            });

    }, [])
    const changeFrameMaterial = (e) => {
        setFrameMaterial(e.currentTarget.value)
        console.log(e.currentTarget.value)
    }
    return (
        <>
            <form>
                <div className="form-group">
                    <label>Brend name</label>
                    <select className="form-control"
                        name={'brendName'}
                        onChange={changeBrend}>
                        <option disabled selected>Choose brend name...</option>
                        {
                            brendArr && brendArr.map(p => {
                                return (
                                    <option value={p}>{p}</option>
                                )
                            })
                        }
                    </select>
                </div>
                <div className="form-group">
                    <label>Type name</label>
                    <select className="form-control"
                        name={'type'}
                        onChange={changeType}>
                        <option disabled selected>Choose type...</option>
                        {
                            typeArr && typeArr.map(p => {
                                return (
                                    <option value={p}>{p}</option>
                                )
                            })
                        }
                    </select>
                </div>
                <Row>
                    <Col>
                        <div className="form-group">
                            <label>Size Wheel</label>
                            <select className="form-control"
                                name={'sizeWheel'}
                                onChange={changeSizeWheel}>
                                <option disabled selected>Choose Size Wheel...</option>
                                {
                                    sizeWheelArr && sizeWheelArr.map(p => {
                                        return (
                                            <option value={p}>{p}</option>
                                        )
                                    })
                                }
                            </select>
                        </div>
                    </Col>
                    <Col>
                        <div className="form-group">
                            <label>Size Frame</label>
                            <select className="form-control"
                                name={'sizeFrame'}
                                onChange={changeSizeFrame}>
                                <option disabled selected>Choose Size Frame...</option>
                                {
                                    sizeFrameArr && sizeFrameArr.map(p => {
                                        return (
                                            <option value={p}>{p}</option>
                                        )
                                    })
                                }
                            </select>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className="form-group">
                            <label>Frame Material</label>
                            <select className="form-control"
                                name={'frameMaterial'}
                                onChange={changeFrameMaterial}>
                                <option disabled selected>Choose Size Frame...</option>
                                {
                                    frameMaterialArr && frameMaterialArr.map(p => {
                                        return (
                                            <option value={p}>{p}</option>
                                        )
                                    })
                                }
                            </select>
                        </div>
                    </Col>
                    <Col>
                        <div className="form-group">
                            <label>Price</label>
                            <input className="form-control"
                                autoComplete="off"
                                name={'price'}
                                placeholder={'Enter Price'}
                                type="text"
                                value={price}
                                onChange={(e) => { setPrice(e.currentTarget.value) }} />
                        </div>
                    </Col>
                </Row>
                <div className="form-group">
                    <img src={productImage ? productImage : null} style={{ width: '100%' }} />
                    <br />
                    <progress value={progress} max="100" style={{ width: '100%' }} />
                    <input type="file" accept=".png,.jpg" onChange={upload} className="btn btn-dark btn-lg btn-block" />
                </div>
                <button type="submit" className="btn btn-dark btn-lg btn-block" onClick={addProductToBD}>Submit</button>
            </form >
            <Table bordered hover variant="dark" >
                <thead>
                    <tr>
                        <th>brendName</th>
                        <th>type</th>
                        <th>sizeWheel</th>
                        <th>sizeFrame</th>
                        <th>frameMaterial</th>
                        <th>price</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        products && products.map(p => {
                            return (
                                <tr>
                                    <td>{p.brendName}</td>
                                    <td>{p.type}</td>
                                    <td>{p.sizeWheel}</td>
                                    <td>{p.sizeFrame}</td>
                                    <td>{p.frameMaterial}</td>
                                    <td>{p.price}</td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
        </>
    )
}
