import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { updateUserName, updateUserPhoto, } from '../../Redux/auth-reducer';
import { ListGroup, Button } from 'react-bootstrap'
import { createUUID } from '../../Shared/UUID';
import firebase from 'firebase';
import ava from '../../Assets/img/ava.gif'

const Profile = (props) => {
    const inputVal = React.createRef()
    let [editMode, setEditMod] = useState(true)
    let [name, setName] = useState(props.displayName)
    let [progress, setProgress] = useState(0)
    useEffect(() => { setName(props.displayName) }, [props.displayName])
    const activeEditMode = () => setEditMod(false)
    const deactiveEditMode = () => {
        setEditMod(true)
        updateUserName(inputVal.current.value)
    }
    const onNameChange = () => setName(inputVal.current.value)
    const upload = (e) => {
        if (props.photoURL) {
            const desertRef = firebase.storage().refFromURL(props.photoURL)
            desertRef.delete().then(() => {
                console.log('Old file deleted successfully');
            }).catch((error) => {
                console.log('Old file deleted error!', error);
            });
        }
        const file = e.target.files[0]
        console.log('Upload works ');
        const filePath = `${createUUID()}.${file.type.split('/')[1]}`;
        const storageRef = firebase.storage().ref().child('userAvatar/' + filePath)
        const uploadTask = storageRef.put(file);
        uploadTask.on('state_changed',
            (snapshot) => {
                const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                setProgress(progress)
            }, (error) => {
                console.log('Upload error ', error);

            }, () => {
                uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
                    console.log('File available at', downloadURL);
                    updateUserPhoto(downloadURL);
                });
            }
        );
    }
    return (
        <>
            <h2>Profile page</h2>
            <ListGroup horizontal>
                <ListGroup.Item>uid :</ListGroup.Item>
                <ListGroup.Item>{props.uid}</ListGroup.Item>
            </ListGroup>
            <ListGroup horizontal>
                <ListGroup.Item>displayName :</ListGroup.Item>
                <ListGroup.Item>
                    <input ref={inputVal} autoFocus={true} disabled={editMode} value={name} onChange={onNameChange} />
                </ListGroup.Item>
                <ListGroup.Item>
                    {editMode
                        ? <Button variant="primary" onClick={activeEditMode}>Edit</Button>
                        : <Button variant="primary" onClick={deactiveEditMode}>Save</Button>
                    }
                </ListGroup.Item>
            </ListGroup>
            <ListGroup horizontal>
                <ListGroup.Item>email :</ListGroup.Item>
                <ListGroup.Item>{props.email}</ListGroup.Item>
            </ListGroup>
            <ListGroup horizontal>
                <ListGroup.Item>emailVerified :</ListGroup.Item>
                <ListGroup.Item><p>{props.emailVerified}</p></ListGroup.Item>
            </ListGroup>
            <ListGroup horizontal>
                <ListGroup.Item>photoURL :</ListGroup.Item>
                <ListGroup.Item>
                    <img src={props.photoURL ? props.photoURL : ava} style={{ width: '200px' }} />
                    <br />
                    <progress value={progress} max="100" style={{ width: '100%' }} />
                </ListGroup.Item>
                <input type="file" accept=".png,.jpg" onChange={upload} />
            </ListGroup>
        </>
    )
}
const mapStateToProps = (state) => ({
    uid: state.firebase.auth.uid,
    displayName: state.firebase.auth.displayName,
    email: state.firebase.auth.email,
    emailVerified: state.firebase.auth.emailVerified,
    photoURL: state.firebase.auth.photoURL,
})
export default connect(mapStateToProps, { updateUserName, })(Profile)